.movie {
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: center;
  align-items: center;
  margin-top: 70px;
  min-height: 500px;

  .genreSelect {
    position: fixed;
    top: 38px;
    left: 9px;
  }

  .movieList {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media all and (orientation: portrait) {
  .movie {
    width: 100%;
    margin: 0;
    padding-top: 220px;

    .genreSelect {
      position: absolute;
      top: 145px;
      left: 5%;
      z-index: 3;

      .select {
        padding: 0;
        position: relative;
        top: 0;
        left: 0;

        h3 {
          margin: 0;
          font-size: 24px;
        }

        .genreContainer {
          font-size: 18px;
        }

        .listContainer {
          .list {
            font-size: 18px;
          }
        }
      }
    }

    .movieList {
      justify-content: center;
      width: 90%;
      padding: 0;
    }
  }
}
